<template>
  <div id="app">
    <h1></h1>
    <h2>Billy Herrington</h2>
    <div class="Billy"><img src="https://static.zain.red/47A70A898214C6F41A65A5538CDF04028DCD7480_size192_w600_h683.jpeg" alt="Billy"></div>
    <p>1969714GVGVNico</p>
    <p>ANIKIA</p>     
    <p>A/B</p>
    <p>20183348FB</p>
    <div><a href="javascript:;" class="btn">{{isCandled ? '' : isCandleing ? '' : ''}}</a></div>
    <h2 class='title'><span class="candleNum">{{candleNum}}</span></h2>
    <p class="candles"><span v-for="n in candleNum" v-bind:key="n"></span></p>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'app',
  data () {
    return {
      candleNum: 21,
      isCandled: false,
      isCandleing: false,
