<style>
body {
  background-color: #000;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #888;
  margin: 60px auto;
  background-color: #000;
  max-width: 600px;
  width: 100%;
}
.Billy {
  width: 100%;
  max-width: 395px;
  display: block;
  margin: 0 auto;
}
h1, h2 {
  font-weight: normal;
}
p {
  text-indent: 2em;
  text-align: left;
}
.btn {
  color: #333;
  font-size: 1.5em;
  background-color: #aaa;
  text-decoration: none;
  box-sizing: border-box;
  border: 1px solid #888;
  padding: 10px 20px;
  border-radius: 5px;
}
.btn:hover {
  background-color: #888;
  border-color: #aaa;
}
.title {
  border-bottom: 1px solid;
  text-align: left;
  margin-top: 60px;
}
.candleNum {
  color: #fff;
}
.candles {
  font-size: 2em;
  text-indent: 0;
}
</style>



